@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');

$primary-color: hsl(158, 36%, 37%);
$secondary-color: hsl(228, 12%, 48%);
$background-color: hsl(30, 38%, 92%);
$text-color-dark: hsl(212, 21%, 14%);
$white-color: hsl(0, 0%, 100%);

$font-family-primary: 'Montserrat', sans-serif;
$font-family-secondary: 'Fraunces', serif;

body {
  background-color: $background-color;
  font-family: $font-family-primary;
  display: flex;
  ;

  height: 100vh;

}

#main {
  background-color: $white-color;
  display: flex;
  width: 600px;
  height: 450px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  margin-left: 420px;
  margin-top: 175px;
}


  #imagem {
    width: 300px;
    height: 450px;
    img {
      width: 300px;
      height: 450px;
      object-fit: cover;
    }
  }

  #conteudo {
    margin-left: 32px;
    display: flex;
    flex-direction: column;
    width: 300px;

    #categoria {
      text-transform: uppercase;
      color: $secondary-color;
      letter-spacing: 5.5px;
      font-size: 11.8px;
      
      padding-top: 32.5px;
      filter: blur(0.5px);
    }

    h1 {
      font-family: 'Fraunces', serif;
      font-weight: 700px;
      color: $text-color-dark;
      font-size: 32px;
      max-width: 200px;
      padding-top: 25px;
      margin: 0;
      column-gap:1px;
      line-height: 30px;

      
    }

    #descricao {
      color: $secondary-color;
      font-size: 14px;
      line-height: 23px;
      margin-bottom: 20px;
      padding-top: 12px;
      font-family: 'Montserrat', sans-serif;
    }

    #preco {
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-direction: row;
     

      #precoatual {
        letter-spacing: 1px;
        margin-top: 5px;
        font-family:'Fraunces', serif;
        font-weight: 700;
        font-size: 30px;
        color: $primary-color;
      }

      #precooriginal {
        text-decoration: line-through;
        color: $secondary-color;
        font-size: 13px;
        margin-right: 76px;
      }
    }

    #botaocarrinho {
      background-color: $primary-color;
      color: $white-color;
      border: none;
      padding: 15px;
      border-radius: 8px;
      cursor: pointer;
      font-family: $font-family-primary;
      font-weight: 700;
      font-size: 14px;
      margin-top: 26px;
      width: 235px;
      

      &:hover {
        background-color: darken($primary-color, 10%);
      }
    }
}